.Leagues {
  width: 70rem;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  &__List {
    font-size: 2.5rem;
    width: 100%;
  }

  &__Button {
    margin: 0 auto;
    text-align: center;
    width: 30rem;
    font-size: 3rem;
  }
}

.Pagination {
  margin: 2rem;
  
  button {
    font-size: 1.5rem;
  }
}