@import '../../shared/scss-variables.scss';

.Matches {
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  &__List {
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  &__Button {
    margin: 2rem auto;
    text-align: center;
    width: 20rem;
    font-size: 2rem;
  }
}

.Pagination {
  margin: 2rem;
  
  button {
    font-size: 1.5rem;
  }
}