.Panels {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.MatchDetail {
  width: 80rem;
}

.Created {
  &__Edit {
    &__Users {
      width: 30rem;
    }

    &__Questions {
      width: 40rem;
    }
  }

  &__Panels {
    display: flex;
    justify-content: space-between;
  }

  h3 {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 1rem;
  }

  &__Users {
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
  }

  &__Questions {
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;

  }

  &__Button {
    margin-top: 2rem;
    width: 20rem;
    font-size: 2rem;
  }

  &__Start {
    width: 100%;
    display: flex;
    justify-content: center;
    
    border-top: 2px solid rgba(#000, 0.2);
  }
}
