@import "../../shared/scss-variables.scss";

.Main {
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 5rem;
  width: 100%;
  gap: 3rem;

  &__Content {

    max-width: 70rem;
    max-height: 50rem;
    font-size: 3rem;
    font-family: "Lato";
    padding: 3rem 4rem;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  &__Promo {
    width: 40rem;
    height: 50rem;
    font-size: 3rem;
    font-family: "Lato";
    
    ol {
      margin-bottom: auto;
      font-size: 2.8rem;
      padding-left: 3rem;
    }
  }

  &__Button {
    width: 30rem;
    margin-bottom: 3rem;
  }
}

@media only screen and (max-width: 1200px) {
  .Main {
    flex-direction: column;
    
    align-items: stretch;

    &__Content {
      max-height: 100rem;
      width: 100%;
    }

    &__Promo {
      width: 100%;
    }
  }
}


.Leaderboard {
  padding: 0.35rem 0;
  
  &Box {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &:not(:last-child) {
    border-bottom: 1px solid rgba(darkgrey, 0.4);
  }
}