.Panels {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.AddQuestion {
  width: 70rem;

  &__Form {
    width: 100%;
  }

  &__Form__Input {
    position: relative;
    margin-bottom: 2.8rem;
    width: 100%;

    input {
      width: 100%;
      padding: 0.5rem 1rem;
      font-size: 2rem;
      outline: none;
      z-index: 100;

      &::placeholder {
        opacity: 0;
      }
    }

    label {
      user-select: none;
      position: absolute;
      top: 0.8rem;
      left: 1rem;
      color: grey;
      font-size: 1.8rem;
      transition: all 0.3s;
      z-index: 100;
    }

    input:focus + label,
    input:not(:placeholder-shown) + label {
      transform: translateY(-3rem) scale(.8);
      color: white;
    }
  }

  &__AnswersLabel {
    display: flex;
    justify-content: space-between;
    font-size: 2rem;
    margin-bottom: 2rem;
  }

  &__Form__Answer {
    position: relative;
    margin-bottom: 2.8rem;
    width: 100%;
    // display: flex;
    // align-items: center;

    input {
      width: 100%;
      padding: 0.5rem 1rem;
      font-size: 2rem;
      outline: none;
      z-index: 100;

      &::placeholder {
        opacity: 0;
      }

      // &[type=text] {
      //   flex-grow: 1;
      // }

      // &[type=radio] {
      //   width: auto;
      //   margin: 0 3rem;
      // }
    }

    &__Label {
      user-select: none;
      position: absolute;
      top: 0.8rem;
      left: 1rem;
      color: grey;
      font-size: 1.8rem;
      transition: all 0.3s;
      z-index: 100;
    }

    input:focus + &__Label,
    input:not(:placeholder-shown) + &__Label {
      transform: translateY(-3rem) scale(.8);
      color: white;
    }

    &__Radio {
      position: absolute;
      top: 0.8rem;
      right: 3rem;

      input {
        position: absolute;
        left: -9999px;
      } 

      input + label {
        position: relative;
        cursor: pointer;
        color: #666;
      }

      input + label:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 20px;
        height: 20px;
        border: 2px solid #ddd;
        border-radius: 100%;
        background: #fff;
      }

      input + label:after{
        content: '';
        width: 10px;
        height: 10px;
        background: #F87DA9;
        position: absolute;
        top: 5px;
        left: 5px;
        border-radius: 100%;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
      }

      input:not(:checked) + label:after {
        opacity: 0;
        transform: scale(0);
      }
      input:checked + label:after {
        opacity: 1;
        transform: scale(1);
      }
    }
  }

  &__ErrorMessage {
    position: absolute;
    top: 1rem;
    right: 1rem;
    color: rgba(rgb(219, 17, 17), 1);
    text-shadow: 2px 2px 5px rgba(black, 0.5);
    font-size: 1.6rem;
    transition: all 0.3s;
    z-index: -10;

    &_show {
      top: -2rem;
    }
  }

  &__Control {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 2rem;
    padding-bottom: 3rem;
    border-bottom: 2px solid rgba(#fff,0.2);

    &__Button {
      text-align: center;
      width: 25rem;
      font-size: 2.5rem;
    }
  }

  &__Button {
    margin: 0 auto;
    text-align: center;
    width: 25rem;
    font-size: 2.5rem;
  }
}