@import '../../shared/scss-variables.scss';

.Footer {
  width: 100%;
  height: 5rem;
  background: linear-gradient(to bottom, transparent , rgba(#000, 0.5) 90%);
  display: flex;
  justify-content: center;
  align-items: center;

  &__Text {
    color: $color-text-light;
    font-size: 2rem;
    font-family: 'Lato', sans-serif;
  }
}