@import "../../shared/scss-variables.scss";

.Menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  width: auto;
  margin-left: auto;
  position: relative;

  font-family: "Lato";
  font-weight: 400;
  font-size: 2rem;

  &__Item {
    margin-left: 4rem;
    color: $color-text-light;

    &_Active > div > button {
      background-color: #fff;
    }
  }

  &__User {
    min-width: 12rem;
    position: relative;

    &__Name {
      text-align: center;
      user-select: none;
      width: 100%;
      height: 100%;
      font-weight: 100;
      padding: 1.5rem 2.5rem;
      font-size: inherit;
      font-family: inherit;
      font-weight: inherit;
      cursor: pointer;
      background-color: $color-pink;
      color: $color-text-light;
    }

    &:hover &__Dropdown {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }

    &__Dropdown {
      text-align: center;
      width: 100%;
      position: absolute;
      right: 0;
      display: flex;
      flex-direction: column;
      z-index: 10;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s;
      transform: translateY(-30%);

      a {
        text-decoration: none;
        color: $color-text-light;
      }

      &__Item {
        width: 100%;
        height: 100%;
        padding: 1.5rem 2rem;
        background-color: rgba($color-pink, 0.7);
      }
    }
  }

  @media only screen and (max-width: 850px) {
    margin-left: 0;
    margin-top: 3rem;
    justify-content: space-around;
    width: 100%;

    &__Item {
      margin-left: 0;
    }
  }
}
