@import '../../shared/scss-variables.scss';

.Logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: $color-text-light;
  
  &__Img {
    height: 100%;
    display: flex;
    align-items: center;
    text-shadow: 2px 2px 2px lawngreen;
  }

  &__Text {
    margin-left: 1.5rem;
    font-size: 5rem;
    font-family: 'Lobster', sans-serif;  
    font-weight: 400;
    background: linear-gradient(to right, gold 0%, gold 3%, transparent 3%), linear-gradient(to right, white, white);
    background-size: 200% 200%;
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    animation: logoAnimation 5s infinite;
    background-position: 100% 0%;
    animation-direction: alternate;
    animation-delay: 5s;
  }
}

@keyframes logoAnimation {
  0% {
    background-position: 100% 0%;
  }

  20% {
    background-position: 210% 0%;
  }

  100% {
    background-position: 210% 0%;
  }
}