@import '../../shared/scss-variables.scss';

.Panel {
  height: auto;
  padding: 3rem 4rem;
  background-color: rgba(#fff, 0.1);
  color: $color-text-light;
  box-shadow: 4px 4px 2px rgba($color-text-dark, 0.3);

  h1 {
    font-size: 3.8rem;
    font-family: 'Lato';
    margin-bottom: 3rem;
    text-align: center;
  }

  p {
    margin-bottom: 4rem;
    font-size: 2.8rem;
  }
}