@import '../../shared/scss-variables.scss';

.FullPage {
  width: 100%;
  height: 100%;
  min-height: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  
  background-image: linear-gradient(to top,  $color-pink, $color-primary-1 85%);
  color: $color-text-light;
}

.CenteredBox {
  width: 75vw;
  display: flex;
  flex-direction: column;
  align-content: stretch;
  height: 100%;

  @media only screen and (max-width: 500px) {
    width: 90vw;
  }
}

.PageContent {
  margin: 2rem 0;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}