.switch-wrapper {
  min-height: inherit;
  height: 100%;
  width: 100%;
  display: grid;
  align-items: start;
}
 
.switch-wrapper > div {
  width: 100%;
  grid-column: 1;
  grid-row: 1;

  display: flex;
  justify-content: center;
}

.CleanLink {
  text-decoration: none;
}

.my-super-cool-portal {
  color: white;
  font-size: 2rem;

  cursor: pointer;
  text-align: center;
}

.my-super-cool-portal td {
  padding: 1rem;
  width: 50%;
}

/* i dont know how to get around this :( */
.my-super-cool-portal td:nth-child(1) {
  width: 70px;
}

.my-super-cool-portal td:nth-child(2) {
  width: 230px;
}

.my-super-cool-portal tr {
  display: table-row;
  text-align: center;
  background-color: #ffb07b33;
  width: 100%;
}