@import '../../shared/scss-variables.scss';

.Quiz {
  width: 80rem;

  &__Question {
    padding: 2rem 1rem;
    background-color: rgba(#fff, 0.1);
    border-radius: 1rem;
    box-shadow: 4px 4px 4px rgba(#000, 0.2);
  }

  &__Answers {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;

    &__Answer {
      cursor: pointer;
      font-size: 2.5rem;
      text-align: center;

      width: 35rem;
      padding: 1.6rem;
      background-color: $color-pink;
      margin-bottom: 2rem;
      box-shadow: 4px 4px 2px rgba(#000, 0.2);
      transition: all 0.2s;

      &:hover {
        box-shadow: 6px 6px 4px rgba(#000, 0.2);
        transform: translate(-2px, -2px);
      }
    }
  }

  &__Waiting {
    font-size: 3rem;
    padding-bottom: 3rem;
  }
}