@import "../../shared/scss-variables.scss";

.Register {
  width: 40rem;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  &__FormBox {
    align-self: flex-start;
    margin-bottom: auto;
    width: 100%;
    margin-bottom: 2rem;
  }

  &__Form {
    width: 100%;
  }

  &__Form__Input {
    position: relative;
    margin-bottom: 2.8rem;
    width: 100%;

    input {
      width: 100%;
      padding: 0.5rem 1rem;
      font-size: 2rem;
      outline: none;
      z-index: 100;

      &::placeholder {
        opacity: 0;
      }
    }

    label {
      user-select: none;
      position: absolute;
      top: 0.8rem;
      left: 1rem;
      color: grey;
      font-size: 1.8rem;
      transition: all 0.3s;
      z-index: 100;
    }

    input:focus + label,
    input:not(:placeholder-shown) + label {
      transform: translateY(-3rem) scale(.8);
      color: white;
    }
  }

  &__ErrorMessage {
    position: absolute;
    top: 1rem;
    right: 1rem;
    color: rgba(rgb(219, 17, 17), 1);
    text-shadow: 2px 2px 5px rgba(black, 0.5);
    font-size: 1.6rem;
    transition: all 0.3s;
    z-index: -10;

    &_show {
      top: -2rem;
    }
  }

  &__Button {
    margin: 0 auto;
    text-align: center;
    width: 30rem;
    font-size: 3rem;
  }

  &__LoginInfo {
    font-size: 2rem;
    font-family: 'Lato';
    
    a, a:link, a:active {
      color: $color-text-dark;
    }
  }
}