@import '../../shared/scss-variables.scss';

.Button {
  user-select: none;
  width: 100%;
  height: 100%;

  position: relative;
  font-weight: 100;
  padding: 1.5rem 2.5rem;
  border: none;
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
  cursor: pointer;
  outline:0;

  &__Transparent {
    background-color: transparent;
    color: inherit;
    transition: all 0.2s;

    &:hover {
      &::before {
        transform: translate(4px, -4px);
      }
  
      &::after {
        transform: translate(-4px, 4px);
      }
    }
  
    &:active {
      &::before, &::after {
        transform: translate(0, 0);
      }
    }
  
    &::before {
      content: "";
      border: 2px solid $color-pink;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transform: translate(-4px, -4px);
      transition: all 0.2s;
    }
  
    &::after {
      content: "";
      border: 2px solid $color-pink;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transform: translate(4px, 4px);
      transition: all 0.2s;
    }

    &_Visited > div > .Button {
      &::before {
        border: 2px solid $color-orange;
        transform: translate(0, 0);
      }
    
      &::after {
        border: 2px solid $color-orange;
        transform: translate(0, 0);
      }
    }
  }

  &__Filled {
    color: $color-text-light;
    z-index: 15;
    transition: all 0.3s;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -5;
      background: linear-gradient(to bottom right, $color-primary-3 ,$color-primary-1);
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% + 9px);
      height: calc(100% + 7px);
      padding: 0 5px 5px 0;
      transition: all 0.2s;
      z-index: -10;
      background: linear-gradient(to bottom, $color-text-light, $color-text-light);
      clip-path: polygon(0 0, 97% 0, 100% 8%, 100% 100%, 6% 100%, 0 90%);
      transition: all 0.2s;
    }

    &:hover {
      transform: translate(-3px, -3px);

      &::after {
        width: calc(100% + 13px);
        height: calc(100% + 11px);
        clip-path: polygon(0 0, 96% 0, 100% 10%, 100% 100%, 6% 100%, 0 85%);
      }
    }

    &:active {
      transform: translate(9px, 7px);

      &::after {
        width: 100%;
        height: 100%;
        clip-path: polygon(0 0, 97% 0, 97% 0, 97% 90%, 0 90%, 0 90%);
      }
    }
  }
}